body {
  background-color: gray;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.container {
  width: 1024px;
  margin: 0 auto;
}

h1, h2 {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.header {
  background-image: url("header-bg.e930ef5d.png");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 40px 70px 97px;
}

.header h1 {
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 30px;
  line-height: 1.167;
}

.header__title_wrapper {
  align-items: center;
  display: flex;
}

.header__title_wrapper img {
  margin-right: 10px;
}

.header-top {
  justify-content: space-between;
  margin-bottom: 37px;
  display: flex;
}

.header-top ul {
  list-style: none;
  display: flex;
}

.header-top ul li:first-child {
  margin-right: 40px;
}

.header-top ul a {
  text-transform: uppercase;
  color: #fff;
  font-size: 12px;
  line-height: 1.167;
  position: relative;
}

.header-top ul a:after {
  content: "";
  opacity: 0;
  width: 100%;
  height: 100%;
  border-bottom: 3px solid #ff6b08;
  transition: all .5s;
  position: absolute;
  bottom: -20%;
  left: 0;
  box-shadow: inset 0 -1px #ff6b08;
}

.header-top ul a:hover:after {
  opacity: 1;
}

.header__form {
  text-align: center;
  margin: 0 auto;
}

.header input {
  width: 336px;
  background-color: #0000;
  border: none;
  border-bottom: .5px solid #fff;
  padding: 4px 0;
}

.header input::placeholder {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: .875;
}

.main {
  background-color: #fff;
  padding: 60px 70px;
}

#watched:hover, #queue:hover, #watched:focus, #queue:focus {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  width: 136px;
  height: 44px;
  background-color: #ff6b01;
  border: none;
  border-radius: 5px;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  top: 121px;
  left: 241px;
}

.footer {
  background-color: #f7f7f7;
  padding: 30px;
}

.footer__container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer__container p, .footer__container span, .footer__container a {
  color: #545454;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.188;
}

.footer__container img {
  margin: 0 10px;
}

.footer__container span {
  margin-right: 5px;
}

.footer__container a {
  text-decoration: underline;
}

.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 50px auto;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

